
@import url(../../node_modules/wallop/css/wallop.css);
@import url(../../node_modules/wallop/css/wallop--slide.css);
@import url("https://fonts.googleapis.com/css?family=Lato");
html {
  box-sizing: border-box;
  font-size: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
html, body, li, form, fieldset, legend {
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}
fieldset, img {
  border: 0;
}
legend {
  color: #000;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption, th, td {
  text-align: left;
  vertical-align: top;
  font-weight: normal;
}
input, textarea, select {
  font-size: 110%;
  line-height: 1.1;
}
abbr, acronym {
  border-bottom: .1em dotted;
  cursor: help;
}
h1, h2 {
  margin-bottom: 2em;
}
h3 {
  margin-bottom: 1em;
}
body {
  font-family: Lato, serif;
}
.header {
  background: transparent;
  position: fixed;
  height: 4.5em;
  width: 100vw;
  top: 0;
  left: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  z-index: 9;
}
.sticky-header .header {
    background: rgba(0, 0, 0, 0.7);
}
.logo {
  display: none;
  fill: rgba(255, 255, 255, 0.8);
  opacity: 0;
  height: 40px;
  width: auto;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transform: translate(2.5em, 0.5em);
          transform: translate(2.5em, 0.5em);
}
.sticky-header .logo {
    opacity: 1;
}
@media (min-width: 500px) {
.logo {
      display: block;
}
}
.nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  margin: 0;
  position: absolute;
  top: 1em;
  right: 2em;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.nav a {
    display: block;
    text-decoration: none;
    padding: 0.5em;
}
.nav a:link {
      border-bottom: 2px solid transparent;
      color: rgba(255, 255, 255, 0.9);
}
.nav a:visited {
      color: rgba(255, 255, 255, 0.9);
}
.nav a:hover {
      border-bottom: 2px solid rgba(255, 255, 255, 0.4);
      color: rgba(255, 255, 255, 0.8);
}
.hero {
  color: #fff;
  text-align: center;
  height: 80vh;
  background-color: rgba(0, 0, 0, 0.25);
  background-blend-mode: hard-light;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 24vh 0 0;
  position: relative;
  width: 100vw;
}
.title {
  font-size: 2em;
  margin: 0 0 0.2em;
  opacity: 1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  z-index: 1;
}
@media (min-width: 500px) {
.title {
      font-size: 3em;
}
}
.sticky-header .title {
    opacity: 0;
}
.tagline {
  font-size: 1em;
  opacity: 1;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  position: relative;
  z-index: 1;
}
@media (min-width: 500px) {
.tagline {
      font-size: 1.5em;
}
}
.sticky-header .tagline {
    opacity: 0;
}
.photo-title {
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  bottom: 1.5em;
  color: #fff;
  font-size: 0.9em;
  font-weight: bold;
  position: absolute;
  right: 2em;
  z-index: 1;
}
.section {
  padding: 5em 2em;
}
@media (min-width: 500px) {
.section {
      padding: 5em;
}
}
.section--about {
    background: #fff;
}
.section--casestudy {
    background: #ececec;
}
.section--photo {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0;
    height: 100vh;
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: -1;
}
.section--experience {
    background: #ececec;
}
.section--contact {
    background: #fff;
    position: relative;
}
.photo-window {
  height: 20em;
  position: relative;
}
.photo-window .photo-title {
    opacity: 0.8;
}
footer {
  background: #484B54;
  color: #fff;
  font-size: 0.8em;
  padding: 1em;
  position: relative;
  text-align: right;
}
.photo-footer {
  position: absolute;
  right: 5em;
  top: 2em;
}
.svg-mountains {
  fill: #484B54;
  width: 80px;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translate(4em, -40px);
          transform: translate(4em, -40px);
}
.Wallop-buttonNext,
.Wallop-buttonPrevious {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  border: none;
  cursor: pointer;
  height: 40px;
  position: absolute;
  text-indent: -9999px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  width: 40px;
  z-index: 9;
}
.Wallop-buttonNext:hover,
  .Wallop-buttonPrevious:hover {
    background: rgba(0, 0, 0, 0.3);
}
.Wallop-buttonNext:hover:before,
    .Wallop-buttonPrevious:hover:before {
      color: rgba(0, 0, 0, 0.6);
}
.Wallop-buttonNext:before,
.Wallop-buttonPrevious:before {
  color: rgba(0, 0, 0, 0.4);
  font-size: 25px;
  left: 0;
  line-height: 0;
  position: absolute;
  text-align: center;
  text-indent: 0;
  top: 50%;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  width: 100%;
}
.Wallop-buttonNext:before {
  content: "\276F";
}
.Wallop-buttonPrevious:before {
  content: "\276E";
}
.Wallop-buttonPrevious {
  left: 1em;
}
.Wallop-buttonNext {
  right: 1em;
}
.slideshow-projects,
.slideshow-mobile {
  border-radius: 3px;
  box-shadow: 0 0 0 2px grey, 0 0 0 15px #ececec, 0 0 0 17px grey;
  margin: 2em 1em 3em;
  max-width: 700px;
}
.slideshow-projects .Wallop-list,
  .slideshow-mobile .Wallop-list {
    background: #fff;
}



